.plus-button {
  transform: rotate(0deg);
  transition: transform 0.2s linear;
}

.plus-button.open {
  transform: rotate(135deg);
  transition: transform 0.2s linear;
}

.food-image {
  height: 0;
  opacity: 0;
  transition: opacity 0.1s ease-in-out;
}

.food-image.show {
  height: auto;
  opacity: 1;
  transition: opacity 0.1s ease-in-out;
}

.splide__pagination--custom {
  background-color: #f2f2f2;
  width: 100px;
}

.nav-link:hover > :focus:not(:hover) {
  transform: scale(1.05);
}

.project_fade {
  opacity: 0.25;
}

/* START MENU TYPE 1 */
.nav-menu:hover > :focus:not(:hover) {
  transform: scale(1.05);
}

.nav-menu:hover > *,
.nav-menu:focus-within > * {
  opacity: 0.6;
  transition-duration: 200ms;
}

.nav-menu:hover > :hover,
.nav-menu:focus-within > :focus {
  opacity: 1;
  transition-duration: 200ms;
}

/* END MENU TYPE 1 */

/* START MENU TYPE 2 */
.nav-menu-2:hover > :focus:not(:hover) {
  transform: scale(1.05);
}

.nav-menu-2:hover > *,
.nav-menu-2:focus-within > * {
  opacity: 0.6;
  transition-duration: 200ms;
}

.nav-menu-2:hover > :hover,
.nav-menu-2:focus-within > :focus {
  opacity: 1;
  transition-duration: 200ms;
}

/* END MENU TYPE 2 */

.nav-link {
  position: relative;
  white-space: nowrap;
}

.nav-link::before,
.nav-link::after {
  position: absolute;
  width: 100%;
  height: 1px;
  background: currentColor;
  top: 100%;
  left: 0;
  pointer-events: none;
}

.nav-link::before {
  content: '';
  /* show by default */
}

.link--metis {
  height: auto;
}

.link--metis::before {
  transform-origin: 100% 50%;
  transform: scale3d(0, 1, 1);
  transition: transform 0.3s;
}

.link--metis:hover::before {
  transform-origin: 0% 50%;
  transform: scale3d(1, 1, 1);
}

[data-value] {
  top: 100px;
}

[menu-name]::before {
  content: '';
  display: block;
  height: 75px;
  margin-top: -75px;
  visibility: hidden;
}

.checkbox {
  width: 1em;
  height: 1em;
  background-color: #fff;
  border-radius: 50%;
  vertical-align: middle;
  border: 1px solid #001528;
  appearance: none;
  -webkit-appearance: none;
  outline: none;
  cursor: pointer;
}

.checkbox-field:has(input:checked) {
  background-color: #001528;
  /*border: 1px solid var(--body-font-color);*/
  color: #fff3de;
}

.checkbox-field:has(input:checked) .filter-icon {
  display: none;
  animation-duration: 0.2s;
  animation-name: bounceIn;
}

.check-icon {
  display: none;
}

.checkbox-field:has(input:checked) .check-icon {
  display: flex;
  animation-duration: 0.2s;
  animation-name: bounceIn;
}

@keyframes bounceIn {
  0% {
    transform: scale(0.1);
    opacity: 0;
  }
  60% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
  }
}

.splide__pagination--custom {
  background-color: #000;
}
